.lds-ellipsis {
  position: relative;
}
.lds-ellipsis > div {
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 40px;
  height: 40px;
}
.lds-ellipsis div > div {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #f00;
  position: absolute;
  top: 100px;
  left: 32px;
  -webkit-animation: lds-ellipsis 2.7s cubic-bezier(0, 0.5, 0.5, 1) infinite
    forwards;
  animation: lds-ellipsis 2.7s cubic-bezier(0, 0.5, 0.5, 1) infinite forwards;
}
.lds-ellipsis div:nth-child(1) div {
  -webkit-animation: lds-ellipsis2 2.7s cubic-bezier(0, 0.5, 0.5, 1) infinite
    forwards;
  animation: lds-ellipsis2 2.7s cubic-bezier(0, 0.5, 0.5, 1) infinite forwards;
  background: #71c2cc;
}
.lds-ellipsis div:nth-child(2) div {
  -webkit-animation-delay: -1.35s;
  animation-delay: -1.35s;
  background: #d8ebf9;
}
.lds-ellipsis div:nth-child(3) div {
  -webkit-animation-delay: -0.675s;
  animation-delay: -0.675s;
  background: #5699d2;
}
.lds-ellipsis div:nth-child(4) div {
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
  background: #1d3f72;
}
.lds-ellipsis div:nth-child(5) div {
  -webkit-animation: lds-ellipsis3 2.7s cubic-bezier(0, 0.5, 0.5, 1) infinite
    forwards;
  animation: lds-ellipsis3 2.7s cubic-bezier(0, 0.5, 0.5, 1) infinite forwards;
  background: #71c2cc;
}
.lds-ellipsis {
  width: 200px !important;
  height: 200px !important;
  -webkit-transform: translate(-100px, -100px) scale(1) translate(100px, 100px);
  transform: translate(-100px, -100px) scale(1) translate(100px, 100px);
}

